`, "",{"version":3,"sources":["webpack://./core/src/components/PublicPageMenu/PublicPageMenuEntry.vue"],"names":[],"mappings":";AA+CA;CACA,kDAAA;AACA","sourcesContent":["<!--\n - SPDX-FileCopyrightText: 2024 Nextcloud GmbH and Nextcloud contributors\n - SPDX-License-Identifier: AGPL-3.0-or-later\n -->\n<template>\n\t<NcListItem :anchor-id=\"`${id}--link`\"\n\t\tcompact\n\t\t:details=\"details\"\n\t\t:href=\"href\"\n\t\t:name=\"label\"\n\t\trole=\"presentation\"\n\t\t@click=\"$emit('click')\">\n\t\t<template #icon>\n\t\t\t<slot v-if=\"$scopedSlots.icon\" name=\"icon\" />\n\t\t\t<div v-else role=\"presentation\" :class=\"['icon', icon, 'public-page-menu-entry__icon']\" />\n\t\t</template>\n\t</NcListItem>\n</template>\n\n<script setup lang=\"ts\">\nimport { onMounted } from 'vue'\n\nimport NcListItem from '@nextcloud/vue/components/NcListItem'\n\nconst props = defineProps<{\n\t/** Only emit click event but do not open href */\n\tclickOnly?: boolean\n\t// menu entry props\n\tid: string\n\tlabel: string\n\ticon?: string\n\thref: string\n\tdetails?: string\n}>()\n\nonMounted(() => {\n\tconst anchor = document.getElementById(`${props.id}--link`) as HTMLAnchorElement\n\t// Make the `<a>` a menuitem\n\tanchor.role = 'menuitem'\n\t// Prevent native click handling if required\n\tif (props.clickOnly) {\n\t\tanchor.onclick = (event) => event.preventDefault()\n\t}\n})\n</script>\n\n<style scoped>\n.public-page-menu-entry__icon {\n\tpadding-inline-start: var(--default-grid-baseline);\n}\n</style>\n"],"sourceRoot":""}]);
